.registration-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 400px; /* Adjust width as needed */
  padding: 20px; /* Add padding for spacing */
  border: 2px solid black; /* Border around the container */
  border-radius: 10px; /* Optional: rounded corners */
  background-color: white; /* Optional: add a background */
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2); /* Optional: add a shadow */
}

h2 {
  margin-bottom: 20px; /* Space between heading and form */
}

.form-group {
  display: flex;
  align-items: center; /* Align items in a single row */
  justify-content: flex-start;
  margin-bottom: 10px; /* Space between form rows */
  width: 100%;
}

.Label {
  width: 120px; /* Consistent label width */
  text-align: right; /* Align label text to the right */
  margin-right: 15px; /* Creates gap between label and input */
  font-weight: bold;
}

input {
  width: 100%;
  padding: 8px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  width: 100%; /* Make button full-width */
  padding: 10px;
  background-color: blue;
  color: white;
  border: none;
  cursor: pointer;
  margin-top: 10px;
  border-radius: 4px;
  display: block; /* Ensures button is a block-level element */
}
